.slider {
  cursor: pointer;
  -webkit-transition: all .4s;
  background-image: url("switcher.7ecf6097.svg");
  border-radius: 24px;
  transition: all .5s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.slider:before {
  content: "";
  height: 22px;
  width: 22px;
  background-image: url("slider.5934c140.svg");
  transition: all .5s;
  position: absolute;
  bottom: 0;
  left: -2px;
}

input:checked + .slider {
  background-image: url("switcher-on.cf91b838.svg");
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
